@use './components/sass/mixins.module.scss';

.container{
    @include mixins.flex-column;
    width: 100vw;
    height: 100vh;
    max-height: 100vh;
    overflow: hidden;
}

.container::after{
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    background-image: url('./img/placeholderbg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100vh;
    z-index: -1000;
    overflow: hidden;
}